export const getPathName = (key: string | undefined) => {
  let title = "";
  let typeOfClothing = "";
  if (key === "/girl-cloth") {
    title = "    ";
    typeOfClothing = "Girl";
  }
  if (key === "/boy-cloth") {
    title = "    ";
    typeOfClothing = "Boy";
  }
  if (key === "/others-cloth") {
    title = "  ,   , ";
    typeOfClothing = "Others";
  }
  if (key === "slider") {
    title = "  ,   , ";
    typeOfClothing = "Slider";
  }
  return { title, typeOfClothing };
};
